@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  font-family: 'Lato', sans-serif;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  margin: 25px 0 0 50px;
}

.header__list {
  list-style: none;
  text-decoration: none;
}

.header__item {
  display: inline;
  padding: 4px 30px 7px 30px;
  letter-spacing: 0.061em;
  word-spacing: 1px;
}

.header__item a {
  text-decoration: none;
  color: #000;
  text-transform: uppercase;
  font-size: 14px;
}

.is-active {
  background-color: #d8d8d8;
  border-radius: 4px;
}

.header__item:hover {
  background-color: #d8d8d8;
  border-radius: 4px;
}

.header__right {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  margin-right: 40px;
}

.input-group {
  margin: 0px 34px 0 10px;
  padding: 5px 2px 5px 5px;
  border: 1px solid #d8d8d8;
  border-radius: 3px;
}

.input-group form {
  display: flex;
  align-items: center;
}

.header__input {
  padding-right: 109px;
  padding-left: 5px;
  padding-left-border: 1px solid transparent;
  padding-left-letter-spacing: 0.15em;
  font-stretch: ultra-expanded;
}

.fa-search {
  color: #004f9e;
  margin: 0 7px;
}

.language {
  list-style: none;
  text-decoration: none;
  padding: 0;
}

.language__item {
  display: inline;
  border-radius: 4px;
  margin: 0px 5px;
}

.language__item a {
  text-decoration: none;
  color: #000;
  text-transform: uppercase;
  font-size: 14px;
}

.is-active {
  background-color: #d8d8d8;
  padding: 5px 9px;
}

.variants {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: flex-start;
  margin-left: 10px;
}

.variant {
  max-width: 389px;
}

.variant__title {
  font-size: 25px;
  margin-bottom: 0px;
}

.variant__title a {
  text-decoration: none;
  color: #004f9e;
}

.variant__text {
  margin-top: 10px;
  line-height: 1.65;
  letter-spacing: 0.032em;
  font-weight: 300;
  height: 100px;
}

.variant__text a {
  text-decoration: none;
  color: #000000;
}

.variant__buttons {
  margin-top: 40px;
}

.button-order {
  background: #f18b00;
  border: none;
  color: #fff;
  padding: 14px 28px;
  border-radius: 4px;
  font-size: 16px;
  letter-spacing: 0.03em;
  text-decoration: none;
}

.fa {
  padding-left: 10px;
}

.copyright {
  margin: 50px 50px 180px 50px;
  font-size: 16px;
  word-spacing: 4px;
}

.copyright a {
  text-decoration: none;
  color: #3372b1;
}

.sub-menu {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  margin: 18px 0 0 50px;
}

.sub-menu__list {
  list-style: none;
  margin: 17px 0 0 50px;
  font-size: 20px;
  font-size-line-height: 0.8;
  letter-spacing: 0.05em;
  font-weight: 100;
}

.sub-menu__item {
  display: inline;
  margin: 0 0 0 30px;
}

.sub-menu__item a {
  text-decoration: none;
  color: #3372b1;
}

.active__sub-menu a {
  color: #000;
  border-bottom: 1px solid #f18b00;
}

.sub-menu__item a:hover {
  color: #000;
  border-bottom: 1px solid #f18b00;
}

.deposit {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
}

.deposit__info {
  margin: 40px 0 0 75px;
}

.deposit__info h5 {
  font-size: 16px;
  line-height: 2.5;
  font-weight: 300;
  letter-spacing: 0.03em;
  margin-bottom: 0;
}

.deposit__info h1 {
  margin-top: 0;
  margin-top-font-size: 48px;
  margin-top-line-height: 40px;
  font-weight: 500;
  letter-spacing: 0.047em;
}

.deposit__info p {
  font-size: 20px;
  font-size-line-height: 1.6;
  margin: 57px 0 0 2px;
  padding-right: 0px;
  letter-spacing: 0.035em;
  font-weight: 100;
}

.deposit__info h2 {
  margin: 45px 0 0 2px;
  font-weight: 700;
}

.deposit__info ul {
  margin: 5px 0 0 0px;
}

.deposit__info ul li {
  list-style-type: none;
  padding: 10px 0;
  font-size: 18px;
  letter-spacing: 0.025em;
  line-height: 30px;
}

.deposit__info ul li:before {
  color: #f18b00;
  font-size: 20px;
  content: "â€¢";
  padding-right: 17px;
}

.accordion {
  margin: 50px 86px 0 65px;
}

.accordion__item {
  cursor: pointer;
  padding: 24px 24px 10px 29px;
  width: 100%;
  text-align: left;
  border: 1px solid #d4d4d4;
  outline: none;
  color: #004f9e;
  font-size: 24px;
  background: #fff;
  font-weight: 700;
}

.accordion__item:hover {
  background-color: #f6f6f6;
}

.accordion__item.active {
  border-bottom: none;
  color: #000;
}

.panel {
  padding: 0 18px;
  max-height: 0;
  overflow: hidden;
  border-left: 1px solid #d4d4d4;
  border-right: 1px solid #d4d4d4;
  margin: -1px;
}

.panel p {
  margin-bottom: 40px;
  margin-top: 15px;
  line-height: 1.667;
  font-size: 22px;
}

.panel h4 {
  font-size: 20px;
  padding-top: 15px;
}

.panel a {
  text-decoration: none;
  color: #004f9e;
}

.accordion-link {
  margin: 5px 0 40px 0;
  padding: 0;
  list-style-type: none;
}

.accordion-link a {
  font-size: 17px;
  line-height: 30px;
}

.panel.show {
  opacity: 1;
  max-height: 500px;
  padding: 0 24px 24px 24px;
  border-bottom: 1px solid #d4d4d4;
}

.accordion__item:after {
  content: '+';
  font-size: 39px;
  font-weight: 500;
  color: #3372b1;
  float: right;
  margin-left: 5px;
}

.accordion__item.active:after {
  content: "";
}

.link-more span {
  color: #f18b00;
}
